import { useState } from 'react';
import { useNavigate } from 'react-router-dom'
import axios from '~/api/axios';

const CREATE_PRODUCT_URL = '/product/create';

function CreateProduct() {
    const navigate = useNavigate();
    
    const [name, setName] = useState('');
    const [price, setPrice] = useState(0);
    const [amount, setAmount] = useState(1);
    const [info, setInfo] = useState('');
    const [selectedImage, setSelectedImage] = useState(null);
    function handleCreateProduct(e) {
        e.preventDefault();
        var formData = new FormData();
        formData.append("name",name);
        formData.append("price",price);
        formData.append("amount",amount);
        formData.append("info",info);
        formData.append("image",selectedImage);
        axios.post(CREATE_PRODUCT_URL, formData,{
            headers: {
              'Content-Type': 'multipart/form-data'
            }
        })
        .then(product => {
            navigate(`/product/${product.data.slug}`, { replace: true });
        })
    }
    return (
        <div>
            <div className="md:grid md:grid-cols-3 md:gap-6 mt-20">
                <div className="md:col-span-1">
                    <div className="px-4 sm:px-0">
                        <h3 className="text-lg font-medium leading-6 text-gray-900">Thm sn phm mi</h3>
                        <p className="mt-1 text-sm text-gray-600">
                            Vui lng nhp y  tt c cc thng tin c yu cu.
                        </p>
                    </div>
                </div>
                <div className="mt-5 md:col-span-2 md:mt-0">
                    <form id="createForm" onSubmit={(e) => handleCreateProduct(e)}>
                        <div className="shadow sm:overflow-hidden sm:rounded-md">
                            <div className="space-y-6 bg-white px-4 py-5 sm:p-6">
                                <div className="grid grid-cols-3 gap-12">
                                    <div className="col-span-3 sm:col-span-3">
                                        <label htmlFor="name" className="block text-sm font-medium text-gray-700">
                                            Tn c
                                        </label>
                                        <div className="mt-1 flex rounded-md shadow-sm">
                                            <input
                                                onChange={(e) => setName(e.target.value)}
                                                required
                                                autoFocus
                                                type="text"
                                                name="name"
                                                id="name"
                                                className="block w-full flex-1 rounded-none rounded-r-md border-gray-300 sm:text-sm outline-0 h-10"
                                                placeholder="C nhiu tin"
                                            />
                                        </div>
                                    </div>
                                </div>
                                <div className="grid grid-cols-3 gap-12">
                                    <div className="col-span-3 sm:col-span-3">
                                        <label htmlFor="price" className="block text-sm font-medium text-gray-700">
                                            Gi tin
                                        </label>
                                        <div className="mt-1 flex rounded-md shadow-sm">
                                            <input
                                                onChange={(e) => setPrice(e.target.value)}
                                                required
                                                type="number"
                                                name="price"
                                                id="price"
                                                min="0"
                                                className="block w-full flex-1 rounded-none rounded-r-md border-gray-300 sm:text-sm outline-0 h-10"
                                                placeholder="5"
                                            />
                                        </div>
                                    </div>
                                </div>

                                <div className="grid grid-cols-3 gap-12">
                                    <div className="col-span-3 sm:col-span-3">
                                        <label htmlFor="amount" className="block text-sm font-medium text-gray-700">
                                            S lng
                                        </label>
                                        <div className="mt-1 flex rounded-md shadow-sm">
                                            <input
                                                onChange={(e) => setAmount(e.target.value)}
                                                required
                                                type="number"
                                                name="amount"
                                                id="amount"
                                                min="1"
                                                max="9999999"
                                                className="block w-full flex-1 rounded-none rounded-r-md border-gray-300 sm:text-sm outline-0 h-10"
                                                placeholder="10"
                                            />
                                        </div>
                                    </div>
                                </div>

                                <div>
                                    <label htmlFor="info" className="block text-sm font-medium text-gray-700">
                                        Thng tin con c
                                    </label>
                                    <div className="mt-1">
                                        <textarea
                                            required
                                            onChange={(e) => setInfo(e.target.value)}
                                            id="info"
                                            name="info"
                                            rows="5"
                                            className=" outline-0 mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"
                                            placeholder="Con c ny rt t"
                                        ></textarea>
                                    </div>
                                    <p className="mt-2 text-sm text-gray-500">M t ngn gn v con c .</p>
                                </div>
                                <div>
                                    <label className="block text-sm font-medium text-gray-700">Hnh nh con c</label>
                                    {selectedImage && (
                                        <div className="flex flex-col items-center">
                                            <img alt="nh con c" src={URL.createObjectURL(selectedImage)} />
                                            <br />
                                            <button
                                                className=" bg-red-500 hover:bg-red-700 text-white font-bold py-2 px-4 rounded"
                                                onClick={() => setSelectedImage(null)}
                                            >
                                                Xo nh
                                            </button>
                                        </div>
                                    )}
                                    <div className="mt-1 flex justify-center rounded-md border-2 border-dashed border-gray-300 px-6 pt-5 pb-6">
                                        <div className="space-y-1 text-center">
                                            <svg
                                                className="mx-auto h-12 w-12 text-gray-400"
                                                stroke="currentColor"
                                                fill="none"
                                                viewBox="0 0 48 48"
                                                aria-hidden="true"
                                            >
                                                <path
                                                    d="M28 8H12a4 4 0 00-4 4v20m32-12v8m0 0v8a4 4 0 01-4 4H12a4 4 0 01-4-4v-4m32-4l-3.172-3.172a4 4 0 00-5.656 0L28 28M8 32l9.172-9.172a4 4 0 015.656 0L28 28m0 0l4 4m4-24h8m-4-4v8m-12 4h.02"
                                                    strokeWidth="2"
                                                    strokeLinecap="round"
                                                    strokeLinejoin="round"
                                                />
                                            </svg>
                                            <div className="flex text-sm text-gray-600">
                                                <label
                                                    htmlFor="image"
                                                    className="relative cursor-pointer rounded-md bg-white font-medium text-indigo-600 focus-within:outline-none focus-within:ring-2 focus-within:ring-indigo-500 focus-within:ring-offset-2 hover:text-indigo-500"
                                                >
                                                    <span>Click  ti nh ln</span>
                                                    <input
                                                        required
                                                        onChange={(event) => setSelectedImage(event.target.files[0])}
                                                        id="image"
                                                        name="image"
                                                        type="file"
                                                        className="sr-only"
                                                    />
                                                </label>
                                            </div>
                                            <p className="text-xs text-gray-500">PNG, JPG up to 10MB</p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="bg-gray-50 px-4 py-3 text-right sm:px-6">
                                <button
                                    type="submit"
                                    form="createForm"
                                    className="inline-flex justify-center rounded-md border border-transparent bg-indigo-600 py-2 px-4 text-sm font-medium text-white shadow-sm hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2"
                                >
                                    Save
                                </button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    );
}

export default CreateProduct;
